export const Validate = ({ email, name, password }) => {
    const errors = {};

    if (!email) {
        errors.email = "  .";
    } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
        errors.email = "   .";
    }

    if (!password) {
        errors.password = "  .";
    } else if (password.length < 4) {
        errors.password = "4    .";
    }

    return errors;
}